import React from "react";
import PageMarker from "../../components/layout/PageFiller/PageMarker";

import Container from "react-bootstrap/Container";

import TextComponent from "../../components/PageSection/SectionText/TextComponent";

const textDelivery = `El envo del producto solicitado se realizar en el trmino de 24 horas a partir de recibirse la confirmacin de pago por parte del cliente, a este tiempo se sumar el que le tome al transportista entregarlo en la direccin establecida por el cliente en su pedido.
APC Hardware es responsable del estado de la mercadera hasta el momento que esta sea entregada al transportista, el cliente es responsable de revisar que el producto que le sea entregado se encuentre en perfectas condiciones fsicas y de acuerdo al pedido realizado. Al recibir su pedido deber verificar que los sellos de seguridad se encuentren intactos, en caso de encontrar alguna anomala deber rechazar la entrega y comunicar del particular a APC Hardware.
Los costos de envo fuera de las ciudades de Quito y Guayaquil, sern asumidos por el cliente y de acuerdo al tarifario del transportista.`;

function DeliveryPolicyPage() {
  return (
    <div>
      <TextComponent title="Politicas de Envio" text={textDelivery} />
    </div>
  );
}

export default DeliveryPolicyPage;
